import {recruit_,recruitadd_,recruitshare_} from '../api/recru.js'
export default {
    head: {
        title: '区域会员制，社交电商平台',
         script: [
      {
        src:
          "https://res2.wx.qq.com/open/js/jweixin-1.6.0.js"
      }
    ]
    },
    name: 'home',
    data() {
        return {
            name2:'',
            phone2:'',
            type:[],
            type_id2:'3'
        }
    },
   async created() {
       const res = await recruit_()
       if(res.data.code == 1) {
           this.type = res.data.data
       }
    },
   async mounted() {
        if (process.client) {
            document.title = "区域会员制社交电商平台";
        }
        var localUrl = encodeURIComponent(window.location.href.split('?')[0]);
        const {data} = await recruitshare_({url:localUrl})
        wx.config({
                debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                appId: 'wx74a82c2291be2790', // 必填，公众号的唯一标识
                timestamp:data.data.timestamp, // 必填，生成签名的时间戳
                nonceStr:data.data.nonceStr, // 必填，生成签名的随机串
                signature:data.data.signature, // 必填，签名
                jsApiList: ["updateAppMessageShareData", "updateTimelineShareData"] // 必填，需要使用的JS接口列表，根据引入的微信js版本不同，jsApi名字不同，具体请看文档
        });
            wx.ready(function() {
                wx.updateTimelineShareData({     //分享到朋友圈
                    title: '区域会员制社交电商平台', // 分享标题
                    link: 'https://www.jiunidu.com/seek_agent', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://image.jiunidu.com/web/zsh5_20210303.png', // 分享图标,用绝对路径
                    success: function() {
                        // 用户点击了分享后执行的回调函数
                    },
                });
                wx.updateAppMessageShareData({    //分享到微信好友
                    title: '区域会员制社交电商平台', // 分享标题
                    link: 'https://www.jiunidu.com/seek_agent', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: 'https://image.jiunidu.com/web/zsh5_20210303.png', // 分享图标
                    desc:'整合本地商业资源，培养区域私域流量', //分享描述
                    success: function () {
                    // 设置成功
                    }
                })
            })
         
    },
    methods:{
       async apply(id) {
                const res = await recruitadd_({name:this.name2,phone:this.phone2,type:this.type_id2})
                if(res.data.code === 1) {
                    this.name2 = this.phone2 = ''
                    this.type_id2 = '3'
                    alert(res.data.msg)
                } else {
                    alert(res.data.msg)
                }
        },

    }
}
